<template>
  <div class="container">
    <div class="registerBox">
      <div class="unLogin verifyContent">
        <div class="doll"><img src="/img/doll.png" /></div>
        <h2>忘記密碼</h2>
        <p>請輸入以下資訊來重新設定密碼</p>
        <div class="formRow">
          <h3>Email *</h3>
          <input
            id="email"
            type="email"
            required
            v-model="Email"
            placeholder="請輸入您的email帳號"
          />
        </div>
        <div class="formRow">
          <h3>驗證碼 *</h3>
          <div class="verify">
            <input id="verifycode" type="number" required v-model="AuthCode" />
            <img v-if="Captcha" :src="Captcha.Image" alt="" @click="load" />
          </div>
        </div>
        <p v-if="Sent">密碼重設驗證已發送，請至您的信箱收信</p>
        <div class="submitBtn" v-if="CanSend">
          <a href="javascript:void(0);" @click="login">發送驗證信</a>
        </div>
        <div class="waitting" v-else>
          <a href="javascript:void(0);">{{ TimeToSend }}秒後可重新發送驗證信</a>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
const TIME_TO_SEND = 60;

export default {
  data() {
    return {
      Email: "",
      AuthCode: "",
      Captcha: null,
      CanSend: true,
      Sent: false,
      TimeToSend: TIME_TO_SEND,
    };
  },
  mounted() {
    this.setBase("register page");
    this.load();
  },
  methods: {
    async load() {
      this.Captcha = await this.$api.getCaptcha();
    },
    async login() {
      if (!this.CanSend) {
        return;
      }
      if (!this.validate(".registerBox")) {
        return;
      }
      try {
        var result = await this.$api.sendForgetPassword({
          Email: this.Email,
          AuthCode: this.AuthCode,
          Secret: this.Captcha.Secret,
        });
        this.Sent = true;
        this.TimeToSend = TIME_TO_SEND;
        this.CanSend = false;
        var cd = () => {
          this.TimeToSend--;
          if (this.TimeToSend > 0) {
            setTimeout(cd, 1000);
          } else {
            this.CanSend = true;
          }
        };
        setTimeout(cd, 1000);

        this.load();
        this.AuthCode = "";
      } catch (e) {
        console.error(e);
        if (e.Status == 9) {
          alert("驗證碼錯誤，請重新輸入");
        } else if (e.Status == 3) {
          alert("查無此帳號");
        } else if (e.Status == 14) {
          alert("請到教育雲進行修改");
        } else {
          alert("登入失敗，帳號或密碼錯誤");
        }
      }
    },
  },
};
</script>